
<template>
    <div class="modalBg" v-show="openModal"
         transition="dialog-bottom-transition"
         scrollable
         style="
        position: fixed;
        z-index: 555;
        top: 5%;
        left: 50%;
        width: 49%;
        height: 100%;"
        >
        <v-card tile>
            <v-toolbar
                    flat
                    dark
                    color="#F5DF4D">
                <v-btn
                        icon
                        dark
                        @click="openModal = false"
                >
                    <v-icon>mdi-close</v-icon>
                </v-btn>

                <v-toolbar-title >QR결제내역</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn
                            dark
                            text
                            @click="openModal = false"
                    >
                        저장
                    </v-btn>
                </v-toolbar-items>
            </v-toolbar>
            <v-card-text style="background-color: #939597">
                <v-col cols="4">
                    <!--<v-card style="background-color: #F5DF4D">-->
                        <v-btn
                                color="#F5DF4D"
                                large
                                class="ma-2"
                                @click="dialog2 = !dialog2"
                        >
                            결제금액 설정
                        </v-btn>
                    <!--</v-card>-->
                </v-col>
                <v-spacer></v-spacer>
                <v-divider class="mb-4 mt-2"></v-divider>
                <v-data-table
                        height="65vh"
                        :headers="headers"
                        :items="desserts"
                        hide-default-footer
                        :page.sync="page"
                        :items-per-page="itemsPerPage"
                        class="elevation-1"
                        @page-count="pageCount = $event"
                ></v-data-table>
                <v-pagination
                        class="pt-6"
                        v-model="page"
                        :length="pageCount"
                ></v-pagination>
                <v-divider></v-divider>
            </v-card-text>
        </v-card>
        <!--<div v-show="dialog2"-->
             <!--style="-->
                         <!--position: fixed;-->
                         <!--z-index: 99999999;-->
                         <!--background-color: black;-->
                         <!--opacity: 0.5;-->
                         <!--top: 5%;-->
                        <!--left: 50%;-->
                        <!--width: 49%;-->
                        <!--height: 100%;"-->
        <!--&gt;</div>-->
        <v-dialog
                v-model="dialog2"
                max-width="500px"
                hide-overlay
                style="position: absolute;">
            <v-card>
                <v-card-title>
                    결제금액설정
                </v-card-title>
                <v-card-text>
                    <v-row>
                        <v-col cols="4">
                            <v-subheader>성인</v-subheader>
                        </v-col>
                        <v-col cols="8">
                            <v-text-field
                                    v-model="adult.price"
                                    value="0"
                                    suffix="원"
                            ></v-text-field>
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="4">
                            <v-subheader>청소년</v-subheader>
                        </v-col>
                        <v-col cols="8">
                            <v-text-field
                                    v-model="teenager.price"
                                    value="0"
                                    suffix="원"
                            ></v-text-field>
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="4">
                            <v-subheader>어린</v-subheader>
                        </v-col>
                        <v-col cols="8">
                            <v-text-field
                                    v-model="child.price"
                                    value="0"
                                    suffix="원"
                            ></v-text-field>
                        </v-col>
                    </v-row>

                </v-card-text>
                <v-card-actions>
                    <v-btn
                            color="primary"
                            text
                            @click="payMonySetting()"
                    >
                        Close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>


<script>
    import {QRpayCheckHistory} from '../api/index.js'
    export default {
        components:{

        },
        props: {
            showModal:{ type:Boolean, default: false },
            // showModalProps: {type: Boolean}
        },
        data(){
            return{
                openModal: this.showModal,
                dialog2:false,
                page: 1,
                pageCount: 0,
                itemsPerPage: 15,
                headers:[
                    { text: 'NO', width:"5%",align: 'start', sortable: false, value:'idx',},
                    { text: '결제일자',width:"50%", align: 'start', sortable: false, value: 'paymentDate',},
                    { text: '타입',width:"30%", align: 'start', sortable: false, value: 'type',},
                    { text: '결제여부',width:"15%", align: 'start', sortable: false, value: 'amount',},
                ],
                desserts: [],
                adult:{type:"1",price:null},
                teenager:{type:"2",price:null},
                child:{type:"3",price:null},
                total:{}

            }
        },
        watch: {
            showModal:function(data) {
                this.openModal = this.showModal;
                console.log(this.openModal)
            }
        },
        created() {
            var vm = this;
            QRpayCheckHistory()
                .then(function(response){
                    vm.desserts = response.data.content.list;

                    console.log(vm.desserts)
                })
                .catch(function(error){
                    console.log(error);
                })
        },
        methods:{
            payMonySetting(data) {
                console.log(data)

            }

                // eventBus.$data(settingDAta)

                // settingMoney



        },


    }
</script>
<style>
    .v-dialog__content .v-dialog__content:active {
        /*width: 50% !important;*/
        width: 50% !important;
    }
</style>
